#intro-container {
    max-width: 80%;
    margin: 20px auto;
    border-radius: 5px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    background-color: #f4f4f4;
    padding: 50px;
}

#intro-title {
    font-size: 2.5em;
    color: #333;
    margin-bottom: 20px;
}

#intro-text {
    font-size: 1.2em;
    color: #666;
    line-height: 1.5;
    margin-bottom: 20px;
    width: 60%;
}

#intro-quote {
    font-style: italic;
    color: #333;
    max-width: 60%;
}

@media only screen and (max-width: 430px) {
    #intro-container {
        max-width: 100%;
        margin: 20px auto;
        border-radius: 5px;
        box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
        display: flex;
        flex-direction: column;
        align-items: center;
        text-align: center;
        background-color: #f4f4f4;
        padding: 10px;
    }
    
    #intro-title {
        font-size: 2em;
        color: #333;
        margin-bottom: 20px;
    }

    #intro-text {
        font-size: 1.2em;
        color: #666;
        line-height: 1.5;
        margin-bottom: 20px;
        width: 90%;
    }

    #intro-quote {
        font-style: italic;
        color: #333;
        max-width: 90%;
        padding-bottom: 20px;
    }
}